import Box from "@mui/material/Box";
import Button from "@mui/material/Button";
import Modal from "@mui/material/Modal";
import * as React from "react";
import { User } from "../../schema/user";

const style = {
  position: "absolute" as "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  width: 400,
  bgcolor: "background.paper",
  border: "2px solid #000",
  boxShadow: 24,
  pt: 2,
  px: 4,
  pb: 3,
};

interface props {
  row: User;
  onDelete(id: number): void;
}

const UserDelete: React.FunctionComponent<props> = ({row, onDelete}) => {
  const [open, setOpen] = React.useState(false);
  const handleOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
  };

  return (
    <div>
      <Button onClick={handleOpen} variant="outlined" color="error">
        Delete
      </Button>
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="parent-modal-title"
        aria-describedby="parent-modal-description"
      >
        <Box sx={{ ...style, width: 400 }}>
          <h2 id="parent-modal-title">Do you want to delete {row.name}?</h2>
          <p id="parent-modal-description">
            This action can not be undone!
          </p>
          <Box sx={{display: "flex", gap: 2, justifyContent: "end"}}>
              <Button onClick={handleClose} variant="outlined" color="info">Cancel</Button>
              <Button onClick={() => onDelete(row.id)} variant="contained" color="error">Delete</Button>
          </Box>
        </Box>
      </Modal>
    </div>
  );
};

export default UserDelete;
